html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Exo", sans-serif;
  font-size: 1rem;
  background-color: #EFEFEF;
  display: block;
  color: #112B3C;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  padding: 0;
}

.title {
  font-size: 5rem;
  margin-bottom: 2rem;
  color: #a87c1c;
}

.display-none {
  display: none;
}

p {
  font-size: 2rem;
  margin: 0;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.5rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.5rem;
}

.header {
  border-bottom: 1px solid #dfe0e0;
}
.header .header__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 3.5rem 0 2rem;
  height: 6rem;
  background-color: #EFEFEF;
}
.header .header__nav a {
  padding: 0.4rem 1.5rem 0.3rem 1.5rem;
  font-size: 1.5rem;
  color: #112B3C;
  border-bottom: 0.1rem solid transparent;
  border-radius: 0.3rem;
  background-color: transparent;
}
.header .header__nav a:hover {
  border-bottom: 0.1rem solid #a87c1c;
  background-color: #dfe0e0;
}
.header .header__nav a:active {
  background-color: #FFFFFF;
}
.header .header__nav .header__nav--navigation {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.header .header__nav .header__nav--navigation a {
  margin-right: 2rem;
}
.header .header__nav .header__nav--navigation .logo-nav {
  background-color: transparent;
  border-bottom: 0rem;
  margin: 0;
  padding-right: 0;
}
.header .header__nav .header__nav--navigation .logo-nav img {
  width: 3rem;
  height: auto;
}
.header .header__nav .header__nav--navigation .title-nav {
  padding: 0 2rem 0 1rem;
  margin-right: 3rem;
  font-family: "Satisfy", cursive;
  font-size: 2.5rem;
  color: #a87c1c;
  background-color: transparent;
  border-bottom: 0rem;
}
.header .header__nav .header__nav--navigation .title-nav span {
  color: #112B3C;
}
.header .header__nav .header__nav--access {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.header .header__nav .header__nav--access a {
  margin-left: 2rem;
}
.header .header__nav .header__nav--access .signup-nav {
  padding: 0.4rem 1.5rem 0.3rem 1.5rem;
  font-size: 1.5rem;
  font-weight: 700;
  color: #EFEFEF;
  border-radius: 0.3rem;
  background-color: #F66B0E;
}
.header .header__nav .header__nav--access .signup-nav:hover {
  background-color: #d6522a;
}
.header .header__nav .header__nav--access .signup-nav:active {
  background-color: #F66B0E;
}

.footer {
  display: block;
  background-color: #112B3C;
}
.footer .footer__contact {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #112B3C;
  padding: 2rem 15rem 0 15rem;
  height: 15rem;
}
.footer .footer__contact a {
  padding: 0.5rem 2rem;
  font-size: 1.5rem;
  color: #112B3C;
  border-radius: 0.3rem;
  background-color: #FFFFFF;
}
.footer .footer__contact a:hover {
  background-color: #a87c1c;
}
.footer .footer__contact a:active {
  background-color: #e2be70;
}
.footer .footer__contact .footer__contact--networks {
  margin-left: 9rem;
}
.footer .footer__contact .footer__contact--networks a {
  background-color: transparent;
  border-bottom: 0;
  padding: 0;
  margin-left: 2rem;
}
.footer .footer__contact .footer__contact--networks .icon-footer {
  font-size: 3rem;
  color: #EFEFEF;
}
.footer .footer__contact .footer__contact--networks .icon-footer:hover {
  color: #a87c1c;
}
.footer .footer__contact .footer__contact--networks .icon-footer:active {
  color: #e2be70;
}
.footer .footer__nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #112B3C;
  padding: 0 20rem;
  height: 3rem;
}
.footer .footer__nav a {
  font-size: 1.3rem;
  padding: 0.1rem 0.5rem;
  margin: 0 15rem;
  color: #EFEFEF;
  border-bottom: 0.1rem solid transparent;
}
.footer .footer__nav a:hover {
  border-bottom: 0.1rem solid #d6522a;
}
.footer .footer__text {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 2rem;
  text-align: center;
  letter-spacing: 0.3rem;
  background-color: #000000;
}
.footer .footer__text p {
  font-size: 1.2rem;
  color: #EFEFEF;
}

.cover {
  display: grid;
  grid-template-columns: 60% 40%;
  gap: 2rem;
  padding: 7rem 0;
}
.cover .cover__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.cover .cover__text h2 {
  font-size: 2rem;
  margin-bottom: 3rem;
}
.cover .cover__text .input-cover {
  display: grid;
  grid-template-columns: 80% 20%;
  gap: 0.5rem;
  width: 70%;
  height: 5rem;
}
.cover .cover__text .input-cover .input-cover__fieldset {
  border-radius: 0.7rem;
  border: 1px solid #dfa525;
}
.cover .cover__text .input-cover .input-cover__fieldset .input-cover__input {
  font-size: 1.7rem;
  color: #5e6166;
  padding: 0 1rem;
  width: 100%;
  height: 100%;
  border: none;
}
.cover .cover__text .input-cover .input-cover__button {
  display: flex;
  flex-direction: column-reverse;
  height: 5rem;
}
.cover .cover__text .input-cover .input-cover__button .input-cover__submit {
  border: none;
  width: 95%;
  height: 4.6rem;
  background-color: #dfa525;
  color: #EFEFEF;
  font-size: 2rem;
  margin: 0;
  border-radius: 0.7rem;
  font-weight: 700;
}
.cover .cover__text .input-cover .input-cover__button .input-cover__submit:hover {
  background-color: #a87c1c;
}
.cover .cover__text .input-cover .input-cover__button .input-cover__submit:active {
  background-color: #e2be70;
}
.cover .cover__image {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.cover .cover__image img {
  width: 40rem;
  height: auto;
}

.faqs {
  display: block;
  border-radius: 2rem;
  border: 1px solid #e2be70;
  padding: 0 0 2rem 0;
  margin-bottom: 7rem;
}
.faqs .faqs-title {
  padding: 2rem;
  background-color: #e9ddc4;
  border-radius: 2rem 2rem 0 0;
}
.faqs .faqs__container {
  display: block;
  border-bottom: 1px solid #e2be70;
  padding: 0;
  margin: 2rem 2rem 0 2rem;
  cursor: pointer;
}
.faqs .faqs__container .faqs__question {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
}
.faqs .faqs__container .faqs__question .faqs__question--text {
  font-size: 2.1rem;
  padding: 1.7rem 0 2rem 0;
}
.faqs .faqs__container .faqs__question .faqs__question--button {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 3.5rem;
  padding: 0 0 2rem 0;
}
.faqs .faqs__container .faqs__question .faqs__question--button .icon-angle-circled-down,
.faqs .faqs__container .faqs__question .faqs__question--button .icon-angle-circled-up {
  font-size: 1.5rem;
  margin-left: 0.7rem;
  color: #dfa525;
}
.faqs .faqs__container .faqs__question .faqs__question--button p {
  font-size: 1.5rem;
  font-weight: 700;
  color: #dfa525;
}
.faqs .faqs__container .faqs__answer {
  font-size: 1.75rem;
  padding: 0 0 1.7rem 0;
}

.register {
  display: grid;
  grid-template-columns: 55% 45%;
  align-items: center;
  width: 95%;
  height: calc(100vh - 13.1rem);
  max-width: 85rem;
  margin: 0 auto;
  padding: 0;
}
.register .register__info {
  display: block;
  width: 80%;
}
.register .register__info h3 {
  font-weight: 400;
  color: #a87c1c;
  margin: 1.7rem 0;
}
.register .register__info p {
  font-size: 1.7rem;
  line-height: 3rem;
}
.register .register__signup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.register .register__signup .register__signup--title {
  font-size: 2.5rem;
  font-weight: 400;
  margin-bottom: 2rem;
}
.register .register__signup .register__signup--input {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  border-radius: 2rem;
  width: 90%;
  background-color: #ebe6d9;
}
.register .register__signup .register__signup--input .register-label {
  font-size: 1.7rem;
  font-weight: 700;
  margin: 2rem 0 0.3rem 0;
}
.register .register__signup .register__signup--input .register-input {
  font-size: 1.5rem;
  border: 1px solid #112B3C;
  border-radius: 0.7rem;
  padding: 0.5rem 0.7rem;
}
.register .register__signup .register__signup--input .register-submit {
  padding: 1rem 0;
  margin: 3.5rem 0 2rem 0;
  font-size: 1.5rem;
  font-weight: 700;
  color: #EFEFEF;
  border: 0;
  border-radius: 0.7rem;
  background-color: #F66B0E;
  cursor: pointer;
}
.register .register__signup .register__signup--input .register-submit:hover {
  background-color: #d6522a;
}
.register .register__signup .register__signup--input .register-submit:active {
  background-color: #F66B0E;
}
.register .register__signup .register__signup--login {
  font-size: 1.4rem;
  margin: 3rem auto 0 auto;
}
.register .register__signup .register__signup--login a {
  color: #F66B0E;
}

.access {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  height: calc(100vh - 13.1rem);
}
.access .access__title {
  font-size: 2.5rem;
  font-weight: 400;
  margin-bottom: 2rem;
}
.access .access__input {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  border-radius: 2rem;
  width: 30%;
  background-color: #ebe6d9;
}
.access .access__input .access-label {
  font-size: 1.7rem;
  font-weight: 700;
  margin: 2rem 0 0.3rem 0;
}
.access .access__input .access-input {
  font-size: 1.5rem;
  border: 1px solid #112B3C;
  border-radius: 0.7rem;
  padding: 0.5rem 0.7rem;
}
.access .access__input .access-password {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
}
.access .access__input .access-password .access-forgot {
  font-size: 1.3rem;
  color: #F66B0E;
}
.access .access__input .access-submit {
  padding: 1rem 0;
  margin: 3.5rem 0 2rem 0;
  font-size: 1.5rem;
  font-weight: 700;
  color: #EFEFEF;
  border: 0;
  border-radius: 0.7rem;
  background-color: #F66B0E;
  cursor: pointer;
}
.access .access__input .access-submit:hover {
  background-color: #d6522a;
}
.access .access__input .access-submit:active {
  background-color: #F66B0E;
}
.access .access__signup {
  font-size: 1.4rem;
  margin: 3rem auto 0 auto;
}
.access .access__signup a {
  color: #F66B0E;
}

.footer .footer__contact--access {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #112B3C;
  padding: 0 15rem;
  height: 5rem;
}
.footer .footer__contact--access a {
  padding: 0.5rem 2rem;
  font-size: 1.5rem;
  color: #112B3C;
  border-radius: 0.3rem;
  background-color: #EFEFEF;
}
.footer .footer__contact--access a:hover {
  background-color: #a87c1c;
}
.footer .footer__contact--access a:active {
  background-color: #e2be70;
}

.contact-us {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  height: calc(100vh - 13.1rem);
}
.contact-us .contact-us__title {
  font-size: 2.5rem;
  font-weight: 400;
  margin-bottom: 2rem;
}
.contact-us .contact-us__input {
  display: flex;
  flex-direction: column;
  padding: 2rem 4rem;
  border-radius: 2rem;
  width: 55%;
  background-color: #84A9AC;
}
.contact-us .contact-us__input .contact-us-label {
  font-size: 1.7rem;
  font-weight: 700;
  margin: 2rem 0 0.3rem 0;
}
.contact-us .contact-us__input .contact-us-input {
  font-size: 1.5rem;
  border: 1px solid #112B3C;
  border-radius: 0.7rem;
  padding: 0.5rem 0.7rem;
}
.contact-us .contact-us__input .contact-us-submit {
  padding: 1rem 0;
  margin: 3.5rem auto 2rem auto;
  font-size: 1.5rem;
  font-weight: 700;
  color: #EFEFEF;
  border: 0;
  border-radius: 0.7rem;
  background-color: #F66B0E;
  cursor: pointer;
  width: 50%;
}
.contact-us .contact-us__input .contact-us-submit:hover {
  background-color: #d6522a;
}
.contact-us .contact-us__input .contact-us-submit:active {
  background-color: #F66B0E;
}
.contact-us .access__signup {
  font-size: 1.4rem;
  margin: 3rem auto 0 auto;
}
.contact-us .access__signup a {
  color: #F66B0E;
}

.footer .footer__contact--networks {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #112B3C;
  padding: 0;
  height: 5rem;
  width: 22%;
}
.footer .footer__contact--networks .icon-footer--contact {
  font-size: 3rem;
  color: #EFEFEF;
}
.footer .footer__contact--networks .icon-footer--contact:hover {
  color: #a87c1c;
}
.footer .footer__contact--networks .icon-footer--contact:active {
  color: #e2be70;
}