@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {

    display: block;
    background-color: v.$cian-dark;
    
    .footer__contact {

        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        background-color: v.$cian-dark;
        padding: 2rem 15rem 0 15rem;
        height: 15rem;

        a {

            padding: .5rem 2rem;
            font-size: 1.5rem;
            color: v.$cian-dark;
            border-radius: .3rem;
            background-color: v.$white;

            &:hover {

                background-color: v.$gold-dark;

            }

            &:active {

                background-color: v.$gold-light;

            }

        }

        .footer__contact--networks {

            margin-left: 9rem;

            a {

                background-color: transparent;
                border-bottom: 0;
                padding: 0;
                margin-left: 2rem;
                
            }

            .icon-footer {

                font-size: 3rem;
                color: v.$beige;

                &:hover {

                    color: v.$gold-dark;

                }

                &:active {

                    color: v.$gold-light;

                }

            }

        }

    }

    .footer__nav {

        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        background-color: v.$cian-dark;
        padding: 0 20rem;
        height: 3rem;

        a {

            font-size: 1.3rem;
            padding: .1rem .5rem;
            margin: 0 15rem;
            color: v.$beige;
            border-bottom: .1rem solid transparent;

            &:hover {

                border-bottom: .1rem solid v.$orange-dark;

            }

        }

    }

    .footer__text {

        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        height: 2rem;
        text-align: center;
        letter-spacing: .3rem;
        background-color: v.$black;
        
        p {

            font-size: 1.2rem;
            color: v.$beige;

        }

    }

}